<template>
    <yd-layout class="box_start">
        <navBar title="巡检点" slot="navbar"></navBar>
        <div style="margin:.24rem;">

            <yd-cell-group>
                <yd-cell-item>
                    <span slot="left">巡检点名称</span>
                    <span slot="right" style="color: #999">{{data.xjdmc}}</span>
                </yd-cell-item>
                <yd-cell-item>
                    <span slot="left">班次</span>
                    <span slot="right" style="color: #999">{{banciname}}</span>
                </yd-cell-item>
            </yd-cell-group>

            <yd-flexbox>
                <div class="quan"></div>
                <yd-flexbox-item style="padding-left: 0.2rem">巡检项目</yd-flexbox-item>
            </yd-flexbox>
            <div style="height: 100%">
                <div style="padding: .3rem;background: #fff;margin-bottom: .24rem;" v-for="(item, index) in check_items"
                     :key="index">
                    <h3>{{index+1}}、{{item.xjtmms}}</h3>
                    <yd-radio-group color="#028be6" v-model="checkItemsState[index]" style="margin-top: .1rem;">
                        <yd-radio val="0">正常：无隐患</yd-radio>
                        <yd-radio val="1">异常：有隐患</yd-radio>
                    </yd-radio-group>
                    <!--有隐患时显示-->
                    <div class="box_danger" v-if="checkItemsState[index] === '1'">
                        <div class="box_padding2">
                            <div class="box_padding_bottom1">整改人：</div>
                            <div class="box_itemt box_border" @click="onClickCheck(index, false)">
                                <div class="box_item_content">
                                    <span style="color: rgb(217, 217, 217);font-family: '宋体';"
                                          v-if="item.zgren">请选择整改人</span>
                                    <span v-for="p,pi in item.persons" :key="pi">{{p.NAME + ' '}}</span>
                                    <!--<input type="text" placeholder="请选择整改人" :value="personName">-->
                                </div>
                                <img src="../../assets/right.png" alt="" style="width: .4rem;height: .4rem;">
                            </div>
                        </div>
                        <yd-popup v-model="showzgname" position="bottom">
                            <div class="ren_list">
                                <div style="display:flex;padding:.24rem;">
                                    <div style="flex: 1;"></div>
                                    <div>
                                        <yd-button bgcolor="#028be6" color="#fff" style="border-radius:10px;"
                                                   @click.native="onClickCheck(null, true, item)">确定
                                        </yd-button>
                                    </div>
                                </div>
                                <div class="ren_item" v-for="per,pe in zhenggai" :key="pe" @click="onPickPerson(per)">
                                    <div class="ren_item_left">
                                        <div class="ren_item_le">{{nickAvatar(per)}}</div>
                                        <!--<div class="ren_item_le">略</div>-->
                                    </div>
                                    <div class="ren_name">{{per.NAME}}</div>
                                    <div class="ren_radio">
                                        <img
                                            :src="didPickPerson(per) ? require('../../assets/yes_xuan.png') : require('../../assets/no_xuan.png')"
                                            alt="">
                                    </div>
                                </div>
                            </div>
                        </yd-popup>
                        <div class="box_padding2">
                            <div class="box_padding_bottom1">计划整改时间：</div>
                            <el-date-picker @change="onPickTimeFinish"
                                            v-model="zgTimes[index]"
                                            type="datetime"
                                            value-format="yyyy-MM-dd HH:mm:ss"
                                            placeholder="选择日期时间">
                            </el-date-picker>
                        </div>
                        <div class="box_padding2">
                            <div class="box_padding_bottom1">隐患备注：</div>
                            <textarea class="box_item_content search_input"
                                      rows="3"
                                      placeholder="请输入隐患备注"
                                      v-model="item.dangerRemark"></textarea>
                        </div>
                        <div class="box_item box_padding2 box_photo">
                            <div>附件：</div>
                            <UnvFile :imagesUrl="item.photoUrl" :ident="index"
                                     @pickImageFinish="saveDangerImage"
                                     @deleteImage="deleteDangerImage"></UnvFile>
                        </div>
                    </div>
                </div>
            </div>

            <yd-flexbox>
                <div class="quan"></div>
                <yd-flexbox-item style="padding-left: 0.2rem">巡检备注</yd-flexbox-item>
            </yd-flexbox>
            <div class="box_beizhu">
                <div class="box_item box_padding2">
                    <div>备注：</div>
                    <textarea class="box_item_content" cols="30" rows="3" placeholder="请输入巡检备注"
                              v-model="xjremark"></textarea>
                </div>
                <div class="box_item box_padding2 box_photo2">
                    <div>附件：</div>
                    <unphoto v-on:imagesUrl="saveXunjianUrl"></unphoto>
                </div>
            </div>

            <yd-button size="large" bgcolor="#028be6" color="#fff" @click.native="onClickSave">保存</yd-button>
        </div>
    </yd-layout>
</template>
<script>
    import UnvFile from '../../components/UnvFilePicker.vue' //上传图片的插件
    import unphoto from '../../components/unphoto.vue' //上传图片的插件
    import {api_xunjian_info, api_xunjian_zgr, api_xunjian_save} from "../../components/APIKeys";

    export default {
        name: "xunjiandian",
        components: {unphoto, UnvFile},
        data: function () {
            return {
                zgren: true,//选择整改人时,是否显示"请选择整改人"
                // token: window.localStorage.getItem('token'),
                banciId: window.localStorage.getItem('banciId'),//班次ID
                banciname: window.localStorage.getItem('banciname'),//班次名称
                xunjiandianId: window.localStorage.getItem('banciId'),//巡检点ID
                time: '',//时间
                data: {},//通过巡检点ID获取到的数据
                check_items: [], //检查项
                checkItemsState: [], //检查项radio选中的值
                showzgname: false,//是否展开整改人员
                zhenggai: [],//整改人员的数据
                persons: [],//选择后的人员
                xjremark: '',//巡检备注
                xjPhoto: '',//巡检图片
                dangerRemark: '',//隐患备注
                photoUrl: '',//保存子组件传过来的base64,隐患图片
                zgTimes: [], //整改时间
                personName:'',//选择整改人员后显示的内容
            }
        },
        mounted() {
            this.doLoadData();
            this.doLoadName();
        },
        methods: {
            //将巡检图片保存
            saveXunjianUrl: function (data) {
                this.xjPhoto = data;
            },
            //将隐患图片保存
            saveDangerImage(callbackData) {
                let {ident, data} = callbackData;
                let index = parseInt(ident);
                let item = this.check_items[index];
                item.photoUrl = data;
                this.check_items.splice(index, 1, item);
            },
            deleteDangerImage(callbackData) {
                let {ident} = callbackData;
                let index = parseInt(ident);
                let item = this.check_items[index];
                item.photoUrl = '';
                this.check_items.splice(index, 1, item);
            },
            //点击保存
            onClickSave() {
                this.$showDialog();

                // 存在隐患的数据,上传接口
                // let yinhuanItems = this.checkItemsState.map(i => {
                //     // 有隐患
                //     if (i === '1') {
                //         let item = this.check_items[i];
                //         return {...item};
                //     }
                // }).filter(i => !!i);
                // console.log('yinhuanItems', this.check_items, yinhuanItems[0].persons);

                //存储有隐患的数据
                let subItems = [];
                this.check_items.map((item, idx) => {
                    if (this.checkItemsState[idx] == '1') {
                        let selItem = {...item};
                        // console.log('selItem=============',selItem);
                        selItem.time = this.zgTimes[idx];
                        subItems.push(selItem);
                    }
                });
                // return;
                //将数据格式调整为后台需要的格式
                let subItems2 = [];
                for (let i = 0; i < subItems.length; i++) {
                    let {time,dangerRemark,photoUrl,persons} = subItems[i];
                    let ids = [];
                    let names = [];
                    for (let j = 0; j < persons.length; j++) {
                        ids.push(persons[j].USER_ID);
                        names.push(persons[j].NAME);
                    }

                    // console.log('我看看整改时间',subItems[i].time);

                    let item = {};
                    item.jhzgsj = time;
                    item.yhzgr_ids = ids.join(',');
                    item.yhzgrs = names.join(',');
                    item.bz = dangerRemark;
                    item.yhnr = photoUrl;
                    subItems2.push(item);

                    if(item.yhzgrs == ''){
                        this.$showError('请选择整改人');
                        return false;
                    }
                    else if(item.yhzgr_ids == ''){
                        this.$showError('请选择日期时间');
                        return false;
                    }
                    else if(item.bz == ''){
                        this.$showError('请填写隐患备注');
                        return false;
                    }else if(item.yhnr == ''){
                        this.$showError('请上传隐患照片');
                        return false;
                    }
                }
                // console.log('subItems2===============',subItems2);
                // return;
                let jcList = JSON.stringify(subItems2);
                // console.log('jcList====',jcList);
                let jsonData = {
                    // token: this.token,
                    xjd_id: this.xunjiandianId,
                    xjdmc: this.data.xjdmc,
                    bc_id: this.banciId,
                    xjbc: this.banciname,
                    xjbz: this.xjremark,
                    xjtp: this.xjPhoto,
                    jcList: jcList,//检查条目信息
                    // PIC:this.photoUrl,
                };
                // console.log('上传给后台的图片路径',jsonData.PIC);

                // this.$pop();
                // return;
                this.$post(api_xunjian_save, jsonData).then(res => {
                    this.$showDialog(false);
                    if (res.errno == 0) {
                        this.$showSuccess('保存成功');
                        window.history.go(-2);
                    } else {
                        this.$showError('保存失败');
                    }
                })
            },
            //获取数据
            doLoadData() {
                this.$showDialog();
                let jsonData = {
                    // token: this.token,
                    xunjiandian_id: this.xunjiandianId,
                };
                this.$post(api_xunjian_info, jsonData).then(res => {
                    this.$showDialog(false);
                    if (res.errno == 0) {
                        this.check_items = res.data.check_items || []; //记录检查条目
                        this.checkItemsState = this.check_items.map(i => "0"); //默认"无隐患状态"
                        this.data = res.data; //记录基本信息
                        delete this.data.check_items; //删除基本信息中的检查条目
                        for (let i = 0; i < this.check_items.length; i++) {
                            let self = this;
                            self.check_items[i].persons = [];
                            self.check_items[i].time = '';//res.data.check_items[i].time;
                            self.check_items[i].dangerRemark = '';
                            self.check_items[i].photoUrl = '';
                            self.zgTimes[i] = '';
                            self.check_items[i].zgren = true;
                        }
                    }
                })
            },
            //获取企业整改人员
            doLoadName() {
                this.$post(api_xunjian_zgr, {}).then(res => {
                    if (res.errno == 0) {
                        this.zhenggai = res.data;
                    } else {
                        this.$showError('获取整改人员失败');
                    }
                })
            },
            //点击展示整改人员
            onClickCheck(editItemIndex, isClose = true,item) {
                // 显示选择人员
                if (!isClose) {
                    this.editItemIndex = editItemIndex;
                }
                // 确定选择完人员
                else {
                    if (this.persons.length == '') {
                        this.$showError('请选择整改人');
                        return false;
                    }
                    // 将persons数据,导入到对应的item中
                    this.check_items[this.editItemIndex].persons = [...this.persons];
                    //将persons里的名字展示出来
                    // let per = [];
                    // for(let i = 0;i<this.persons.length;i++){
                    //     per.push(this.persons[i].NAME);
                    // }
                    // this.personName = per.join(',');

                    this.persons = []; //清空persons
                    this.editItemIndex = -1; //清空编辑item
                }

                // 控制显隐
                this.showzgname = !this.showzgname;
                item.zgren = false;

            },

            //显示名字最后一个字
            nickAvatar(p) {
                let chs = [...p.NAME];
                return chs[chs.length - 1];
            },
            // 选择具体一个人
            onPickPerson(p) {
                let index = this.persons.indexOf(p);
                if (index >= 0) {
                    this.persons.splice(index, 1);
                }
                else {
                    this.persons.push(p);
                }
            },
            // 判断是否选择一个人
            didPickPerson(p) {
                let index = this.persons.indexOf(p);
                return index >= 0;
            },

            onPickTimeFinish(result) {
                console.log(result);
            },
        }
    }
</script>

<style scoped>
    .quan {
        width: 0.2rem;
        height: 0.2rem;
        border-radius: 0.1rem;
        background-color: #028be6;
    }

    .box_danger {
        border: 1px solid #d9d9d9;
        margin-top: .2rem;
    }

    .box_item {
        display: flex;
        /*padding: 0.2rem;*/
    }

    .box_itemt {
        display: flex;
        /*padding: .2rem;*/
        align-items: center;
    }

    textarea {
        padding: .1rem;
        border: 1px solid #d9d9d9;
        width: 100%;
    }

    .box_item_content {
        flex: 1;
        color: gray;
    }

    .box_beizhu {
        margin-top: .24rem;
        background: #fff;
        padding: .1rem;
    }

    .ren_list .ren_item {
        padding: .24rem;
        border-bottom: 1px solid #d9d9d9;
        display: flex;
        align-items: center;
    }

    .ren_item_left {
        font-size: .36rem;
        background: #028be6;
        width: 0.8rem;
        height: 0.8rem;
        color: #fff;
        display: flex;
        align-items: center;
        margin-right: .24rem;
        border-radius: 50px;
    }

    .ren_item_le {
        flex: 1;
        text-align: center;
    }

    .ren_name {
        flex: 1;
    }

    .ren_radio {
        width: 0.5rem;
        height: 0.5rem;
    }

    .ren_radio img {
        width: 100%;
    }

    .box_border {
        border: 1px solid #d9d9d9;
        height: 40px;
        line-height: 40px;
        padding-left: .1rem;
    }

    .box_padding2 {
        padding: .2rem;
    }

    .box_padding_bottom1 {
        padding-bottom: .1rem;
    }

    textarea::-webkit-input-placeholder {
        color: rgb(217, 217, 217);;
    }
</style>
<style>
    .box_start .yd-radio > input[type=radio]:checked + .yd-radio-icon {
        margin-bottom: .03rem;
    }

    .box_start .yd-radio-icon {
        margin-bottom: .03rem;
    }

    .box_start .yd-radio {
        /*padding-right: 1rem;*/
    }

    .box_start .box_itemt .box_img {
        width: calc(110% / 3);
    }

    .box_start .box_itemt .box_img {
        width: calc(110% / 3);
    }

    .box_start .yd-popup-bottom {
        max-height: 60% !important;
    }

    .box_start .el-input__prefix {
        display: none;
    }

    .box_start .el-input--prefix .el-input__inner {
        padding-left: .1rem;
    }

    .box_start .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 100%;
    }

    .box_start .el-input__inner {
        border: 1px solid #d9d9d9;
        border-radius: 0;
        color: gray !important;
        font-size: .3rem;
    }

    .box_photo .box_img {
        width: calc(108% / 3) !important;
    }

    .box_photo2 .box_img {
        width: calc(98% / 3) !important;
    }
</style>



